import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const state = {
  authed: false,
  user:{}
}

const mutations = {
  set (state, [variable, value]) {
    state[variable] = value
  },
  updateUser(state, [value]) {
    for (const key in value) {
      state.user[key] = value[key]
    }
  },
}

export default new Vuex.Store({
  state,
  mutations
})