<template>
  <div id="room-container">
    Выбор УИК для наблюдения
    <div v-for="(uik, index) in uiks" :key="index" class="single-uik" @click="handleUik(index)">
      <span>Номер: {{uik.id}}</span>
      <span>Наименование: {{uik.name}}</span>
      <span>Адрес: {{uik.address}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'rooms',
  data () {
    return {
      uiks:[]
    }
  },
  mounted: function(){
    this.$http.get('/v1/uik/').then(e=>{
        this.uiks = e.data
    })
  },
  methods:{
    handleUik(index){
      this.$router.push({ name: 'main', query: { uik_id:this.uiks[index].id } })
    }
  }
}
</script>
<style>
#room-container, .single-uik{
  display: flex;
  flex-direction: column;
}
.single-uik{
  margin-bottom: 5px;
  border: 1px solid black;
}
.single-uik:hover{
  background-color: rgba(128,128,128,0.75);
  cursor: pointer;
  color:white;
}
</style>

