<template>
    <div id="chat">
        <div class="chat-header">Чат по УИК {{uik_id}}</div>
        <div class="chat-link"><a target="_blank" :href="tgData.inviteLink">Ссылка на чат</a></div>
        <div class="chat-message-container">
          <Message @nick-clicked="addNick(msg)" :messageEntity="msg" v-for="(msg, index) in existingMessages" :key="index"></Message>
        </div>
        <div class="chat-footer">
            <input v-model="message" type="text">
            <button @click="sendMessage">Отправить</button>
             <CDropdown v-if="!isAfk" color="primary" toggler-text="Отхожу от компьютера" class="m-2">
              <CDropdownItem @click="setAfk(5)"> 5 мин</CDropdownItem>
              <CDropdownItem @click="setAfk(15)"> 15 мин</CDropdownItem>
              <CDropdownItem @click="setAfk(30)"> 30 мин</CDropdownItem>
              <CDropdownItem @click="setAfk(60)"> 1 час</CDropdownItem>
              <CDropdownItem @click="setAfk(0)"> до конца дня</CDropdownItem>
            </CDropdown>
            <CButton color="primary" @click="setAfk(-1)" v-if="isAfk">Вернулся к наблюдению</CButton>
        </div>
    </div>
</template>
 
<script>

import { io } from "socket.io-client";
import Message from '../TelegramChat/Message'

export default {
  name: 'TelegramChat',
  props:{
    uik_id: Number
  },
  components:{
    Message
  },
  data () {
    return {
      isAfk:false,
      message:'',
      tgData:{},
      existingMessages:[],
      socket:undefined
    }
  },
  methods:{
    setAfk(mins){
      if(mins!== -1){
        this.isAfk = true
        const message = `Отхожу от компьютера ${mins?`на ${mins} мин`:'До конца дня'}`
        this.socket.emit("message", {message:message, uik:this.uik_id, telegram_id: this.tgData.telegram_id, user:this.$store.state.user});
      }else{
        this.isAfk = false
        this.socket.emit("message", {message:'На месте', uik:this.uik_id, telegram_id: this.tgData.telegram_id, user:this.$store.state.user});
      }
    },
    addNick(msg){
      this.message += `@${msg.user.username}`
    },
    sendMessage(){
      this.socket.emit("message", {message:this.message, uik:this.uik_id, telegram_id: this.tgData.telegram_id, user:this.$store.state.user});
      this.message=''
    }
  },
  mounted: function(){
    this.socket = io("/", {
      reconnectionDelayMax: 10000,
      query: {
        uik_id: this.uik_id
      }
    });
    this.socket.on("chatMessage", (body) => {
      this.existingMessages.push(body)
    });
    this.socket.on("chatInfo", (c) => {
      Object.assign(this.tgData, c)
    });
  }
}
</script>
<style scoped>
  #chat{
    border:2px solid green;
  }
  .chat-header{
    margin-bottom: 10px;
  }
  .chat-message-container{
      display: flex;
      flex-direction: column;
  }
</style>
