<template>
  <div id="main-container">
    <button @click="logout">logout</button>
    <TelegramChat :uik_id="this.$route.query.uik_id"></TelegramChat>
    <UiStream :options="videoOptions"></UiStream>
  </div>
</template>

<script>
import TelegramChat from './Main/TelegramChat'
import UiStream from './Main/UiStream'

export default {
  name: 'Main',
  components: {
    TelegramChat,
    UiStream
  },
  data () {
    return {
      videoOptions: {
				autoplay: true,
				controls: true,
				sources: [
					{
            src:"https://content.uplynk.com/channel/36953f5b6546464590d2fcd954bc89cf.m3u8",
						// src:"http://www.cikrf.ru/hls/liveatrium/index.m3u8",
            type:'application/x-mpegURL'
					}
				]
			}
    }
  },
  methods:{
    logout(){
      this.$store.commit('set', ['authed',false])
      this.$router.push('/')
    }
  }
}
</script>

