<template>
  <div id="login-container">
    <!-- <img id="logo" src="~@/assets/logo.png" alt="electron-vue"> -->
    <span>Для авторизации перейдите в тг бот <a target="_blank" href="https://t.me/electieBot">@electieBot</a></span>
    <input type="text" v-model="chat_id">
    <button @click="login">Войти</button>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      chat_id:''
    }
  },
  methods:{
    login(){
      this.$http.post('/v1/auth/login', {
          chat_id:this.chat_id
      }).then(e=>{
        if(e.data.status){
          this.$store.commit('set', ['authed', e.data.status])
          this.$store.commit('updateUser', [e.data.user])
          this.$router.push({ name: 'rooms'})
        }
      })
    }
  },
}
</script>
<style>
#login-container{
    width: 400px;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;
    display: flex;
    flex-basis: 0 0;
    margin: auto;
}
</style>

