<template>
    <div class="message-body">
        <div class="message-icon"></div>
        <div class="message-sender" @click="addNick">{{messageEntity.user.username}}:</div>
        <div class="message-text">{{messageEntity.message}}</div>
    </div>
</template>

<script>

export default {
  name: 'ChatMessage',
  props:{
      messageEntity:Object
  },
  data () {
    return {
    }
  },
  methods:{
    addNick(){
      this.$emit('nick-clicked')
    }
  }
}
</script>
<style scoped>
 .message-body{
     display: flex;
 }
 .message-sender:hover{
   cursor: pointer;
 }
</style>