<template>
    <div class="stream-container">
        <input @change="loadStreams" v-model="streams[0].url" type="text">
        <input @change="loadStreams" v-model="streams[1].url" type="text">
        <div class="stream-block">
            <video @click="switchView" controls :style="calcStyle(0)"  id="camera-1"  ref="camera-1"></video>
            <video @click="switchView" controls :style="calcStyle(1)"  id="camera-2"  ref="camera-2"></video>
        </div>
        <button @click="record">{{recording?'Остановить':'Начать'}} запись</button>
    </div>
</template>

<script>
const Hls = require('hls.js')

export default {
    name: "VideoPlayer",
    props: {
    },
    data() {
        return {
            activeCam:0,
            mediaRecorder: null,
            recordedChunks:[],
            recording:false,
            streams:[{
                url:'https://content.uplynk.com/channel/36953f5b6546464590d2fcd954bc89cf.m3u8',
                dataStream:[],
                ref:'camera-1'
            },{
                url:'https://test-streams.mux.dev/x36xhzz/x36xhzz.m3u8',
                dataStream:[],
                ref:'camera-2'
            }]
        }
    },
    computed:{
        
    },
    methods:{
        loadStreams(){
            for (const stream of this.streams) {
                this.initStream(stream)
            }
        },
        record(){
            if(this.recording){
                this.mediaRecorder.stop();
            }else{
                this.recordedChunks = []
                this.$refs[this.streams[this.activeCam].ref].play()
                var stream = this.$refs[this.streams[this.activeCam].ref].captureStream();
                this.mediaRecorder = new MediaRecorder(stream, { mimeType: "video/webm; codecs=vp9" });
                this.mediaRecorder.ondataavailable = (event) => {
                    if (event.data.size > 0) {
                        this.recordedChunks.push(event.data)
                        //fs.appendFile(`./test.webm`, event.data, function(e){console.log(e)});
                    };
                }
                this.mediaRecorder.onstop = (e) => {
                    this.$refs[this.streams[this.activeCam].ref].pause()
                    var blob = new Blob(this.recordedChunks, {
                        type: "video/webm"
                    });
                    var url = URL.createObjectURL(blob);
                    var a = document.createElement("a");
                    document.body.appendChild(a);
                    a.style = "display: none";
                    a.href = url;
                    a.download = `${Date.now()}.webm`;
                    a.click();
                    window.URL.revokeObjectURL(url);
                };
                this.mediaRecorder.start();
            }
            this.recording = !this.recording
        },
        calcStyle(id){
            const main = 'width:640px;height:480px;z-index:0'
            const off = 'width:200px;height:160px;z-index:1'
            return id === this.activeCam? main : off
        },
        switchView(){
            this.activeCam = this.activeCam? 0 : 1
        },
        initStream(entity){
            const hls = new Hls();
            const video = this.$refs[entity.ref]
            hls.loadSource(entity.url);
            hls.attachMedia(video);
            hls.on(Hls.Events.MANIFEST_PARSED, function () {
                video.play();
                // hls.on(Hls.Events.BUFFER_APPENDING, function (event, data) {
                //     console.log(228)
                //     // if(data.type == 'video'){
                //     //     entity.dataStream.push(data.data);
                //     //     //fs.appendFile(`./test.ts`, data.data, function(e){console.log(e)});
                //     // }
                // });
            });
        }
    },
    mounted() {
        this.loadStreams()
    },
    beforeDestroy() {
        if (this.player) {
            this.player.dispose()
        }
    }
}
</script>
<style >
.stream-container{
    margin-top: 10px;
    height: 600px;
    border:2px solid blue;
}
.stream-block{
    width:640px;
    height: 480px;
}
.stream-block video{
    position: absolute;
}
video:hover{
    border: 5px solid green;
}
</style>
